*{
  list-style: none;
  padding: 0;
  margin: 0;
/*   scroll-behavior: smooth; */
}

.teul{
  margin: 0 10px;
  height: 100px;
  overflow: scroll;
  overflow: hidden;
}

.lotto{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 50px;
/*   width: 100px; */
  height: 100px;
/*   background-color: yellow; */
  text-align: center;
  margin: auto;
}

.container{
  width: 80%;
  max-width: 561px;
  margin: 20px;
  z-index: 3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.lotto-str{
  z-index: 4;
  width: 80%;
/*   position: absolute; */
/*   margin: auto; */
}
.wrapper{
  margin-top: -100px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(255,255,255,0.8);
  border-radius: 20px;
  width: 100%;
  height: 250px;
  z-index: 3;
}

.lotto > p{
  justify-contents: center;
}

.button-section{
  margin-top: 20px;
  width: 100%;
  display: flex;
}

.button{
  text-align: center;
  margin: auto;
  background: rgba(196, 196, 196, 0.5);
  background: #EF4D4D;
  border-radius: 10px;
  width: 49%;
  height: 50px;
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.button > p{
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #FFFFFF;
}
.button:hover{
  background: #fff;
}
.button > img{
  width: 50%;
  min-width: 100px;
}