@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR&family=Sunflower:wght@500&display=swap');

.selection{
  text-align: center;
/*   background:  */
  color: #fff;
  font-size: 20px;
  margin-bottom: 50px;
}

*{
  margin: 0;
  padding: 0;
  font-family: 'Sunflower';
  -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
}
*::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Opera*/
}
body{
  background: #000025;
  background: #C5DB91;
}
#root{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

header{
  width: 100%;
/*   height: 800px; */
}

.bg{
  width: 100%;
  max-width: 800px;
}

main{
/*   margin: auto; */
  width: 100%;
  max-width: 800px;
  background-color: #C5DB91;
}

h2{
  display:none;
/*   font-family: 'Inter'; */
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  text-align: center;
  margin: auto;
  width: 300px;
  background-color: #fff;
  padding: 12px 0;
  border-radius: 20px;
}
footer{
  width: 100%;
  background: #000025;
  padding: 40px 0;
}
footer > p{
  color: #fff;
  text-align: center;
  margin-top: 10px;
}
section{
/*   display: flex; */
}

.present{
  margin: 18px auto;
/*   margin: auto; */
  width: 248px;
  height: 274px;
  background-color: #FFFFFF;
  border-radius: 20px;
  box-shadow: 5px 5px 10px 5px rgba(0, 0, 0, 0.2)
}

.present:hover{
/*   width: 250px; */
  outline: 5px solid #1a93f0;
/*   background: #FFD3D3; */
}

.present-num{
  border: 5px solid #FFFFFF;
  background: #F7EB80;
  background: #EF4D4D;

  
  position: relative;
  top: 10px;
  left: 8px;
  box-sizing: border-box;
  border-radius: 20px;
  width: 60px;
  height: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.present-num > p{
/*   font-family: 'Inter'; */
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #000000;
  color: #fff;
}

.present-img{
  width: 140px;
  position: relative;
  top: -20px;
  left: 56px;
}

.present-name{
  text-align: center;
  top: 36.9px;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
  color: #000025;
}